<template>
	<div class="tabs">
		<!-- closable 是判断是否需要添加删除按钮 -->
		<!-- effect 是展示按钮的不同状态下的不同主题 -->
		<el-tag
			v-for="(item, index) in tags"
			:key="item.path"
			:closable="item.name !== 'home'"
			:effect="$route.name === item.name ? 'dark' : 'plain'"
			@click="changeMenu(item)"
			@close="handleClose(item, index)"
			size="small"
		>
			{{ item.label }}
		</el-tag>
	</div>
</template>
<script>
// 辅助函数，vuex
import { mapState, mapMutations } from "vuex";
export default {
	name: "CommonTag",
	data() {
		return {};
	},
	// 计算
	computed: {
		...mapState({
			tags: (state) => state.tab.tabsList,
		}),
	},
	methods: {
		...mapMutations(["closeTag"]),
		// 点击tag跳转的功能
		changeMenu(item) {
			// console.log(item);
			this.$router.push({ name: item.name });
		},
		// 点击tag删除的功能
		handleClose(item, index) {
			// 调用store中的mutation
			// this.$store.commit
			this.closeTag(item);
			const length = this.tags.length;
			// 删除之后的跳转逻辑
			// this.$router.push({ name: item.name });
			if (item.name !== this.$route.name) {
				return;
			}
			// 表示的是删除的最后一项
			if (index === length) {
				this.$router.push({
					name: this.tags[index - 1].name,
				});
			} else {
				this.$router.push({
					name: this.tags[index].name,
				});
			}
		},
	},
};
</script>
<style lang="less" scoped>
.tabs {
	padding: 20px;
	.el-tag {
		margin-right: 15px;
		cursor: pointer;
	}
}
</style>
